nav {
  text-align: center; 
  /* display: flex; */
  /* justify-content: center; */
  max-width: 500px;
  height: 50px;
  box-shadow: 0px -10px 20px #00000023;
  border-radius: 40px 40px 0px 0px;
  background-color: white;
  /* border-top: 1.5px solid lightgray;
  border-right: 1.5px solid lightgray;
  border-left: 1.5px solid lightgray;  */
  bottom: 0;
  left: 0;
  right: 0;
  margin: 0 auto;
  z-index: 9999; 
}; 
.home { 
  margin: 0 auto;
  text-align: center; 
  max-width: 500px;
  max-height: 640px; 
  height: 100vh;
  background-color: white; 
  border-radius: 0px 0px 40px 40px;  
  padding: 0;
  font-family: "Quicksand", sans-serif;
  color: darkslategray; 
}

button {
  background-color: tomato;
  border: none; 
  padding: 10px; 
  font-family: "Quicksand", sans-serif;
  font-weight: 400;  
  border-radius: 10px;
}



/*====== WIDTH ======*/
.screenW {
  width: 100vw;
}
.fullW {
  width: 100%;
}
/*====== HEIGHT ======*/
.screenH {
  height: 60vh;
}
.fullH {
  height: 100%;
}
.height70 {
  height: 70px;
}
/*====== BG COLOR ======*/
.dGray {
  background-color: #333;
}
.white {
  background-color: #333;
}
.white50 {
  background-color: #ffffff80;
}
.black50 {
  background-color: #00000080;
}
/*====== FONT ======*/
/* .fSize2 {
  font-size: 2rem;
} */
/*====== IMAGE ======*/
.imgCover {
  object-fit: cover;
}
/*====== OVERFLOW ======*/
.overflowH {
  overflow: hidden;
}
/*====== TRANSITIONS ======*/
.transition1l {
  transition: left 1s ease-in-out;
}
/*====== POSITION ======*/
.abs {
  position: absolute;
}
.rel {
  position: relative;
}

/* making it smooth */
.left0 {
  left: 0px;
}
.left100vw {
  left: 100vw;
}
.leftM100vw {
  left: -100vw;
}
.bot0 {
  bottom: 0;
}
/*====== FLEX ======*/
.df {
  display: flex;
}
.row {
  display: flex;
  flex-direction: row;
}
.col {
  display: flex;
  flex-direction: column;
}
.fCenter {
  display: flex;
  justify-content: center;
  align-items: center;
}
.flex1 {
  flex: 1 1;
}
.flex8 {
  flex: 8 1;
}

